@import './variables';
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap" rel="stylesheet">');
@import './button';

* {
    font-family: 'Lato' , sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,
li,
dl {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
    display: inline-block;
    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

p {
    margin: 0;
    padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; 
}

.app {
    width: 1100px;
    margin: 0 auto;
    padding: 50px 0 50px 0;
    position: relative;
    .bg-decoration {
        position: absolute;
        right: -30px;
        bottom: 0px;
        z-index: -1;
    }
}

main {
    margin-top: 50px;
    position: relative;
}

.pulse {
    animation: pulse 1.5s ease-in-out .5s infinite
}

@keyframes pulse {
    0% {
        opacity: 1
    }
    50% {
        opacity: .4
    }
    100% {
        opacity: 1
    }
}
.fadeIn {
    animation: 0.5s fadeIn forwards;
}
  
.fadeOut {
animation: 0.5s fadeOut forwards;
}
  
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(90%);
    }   
    to {
        opacity: 1;
        transform: scale(100%);
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        transform: scale(100%);
    }
    to {
        transform: scale(90%);
        opacity: 0;
    }
}